.no-webp .start {
    background-image: url(build/img/header.jpg);
}
.webp .start {
    background-image: url(../img/header.webp);
}

.header{
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;

    &.start {

        background-position: center center;
        background-size: cover;

        @include tablet{
            min-height: 70rem;
        }

        .container-header{
            @include tablet{
                min-height: 70rem;
            }
        }
    }
}

.container-header{
    
    display: flex;
    flex-direction: column;
    justify-content:space-between ;

    h1{
        text-align: left;
        color: $blanco;
        font-weight: $bold;
        line-height: 2;
        max-width: 60rem;
    }
}