body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.centered-content {
  max-width: 80rem;
}

.align-right {
  display: flex;
  justify-content: right;
}

.btn-yellow {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.btn-yellow:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-yellow-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.btn-yellow-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-green {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.btn-green:hover {
  background-color: #619800;
  cursor: pointer;
}

.btn {
  width: 25%;
}

.botones {
  display: flex;
  justify-content: space-around;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p, .dark-mode a, .dark-mode blockquote {
  color: #FFFFFF;
}
.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #cccccc !important;
}
.dark-mode .advertisement {
  background-color: #404040;
  border: none;
  margin-bottom: 2rem;
}
.dark-mode .advertisement:last-of-type {
  margin-right: 0;
}
.dark-mode .propertySection {
  background-color: #404040;
  padding: 0.5rem 1rem 1rem 1rem;
  margin-top: 2rem;
}
.dark-mode .propertySection .iconC {
  filter: invert(100%);
}
.dark-mode .propertySection .price {
  color: #71B100;
}
.dark-mode .container-advertisements {
  background-color: #1a1a1a;
}
.dark-mode .form label,
.dark-mode .form legend {
  color: #e1e1e1;
}
.dark-mode .form p {
  color: #aeaeae;
}
.dark-mode .form input:not([type=submit], [type=radio]),
.dark-mode .form textarea,
.dark-mode .form select {
  background-color: #333333;
  color: #FFFFFF;
  border: #4d4d4d;
}

.footer {
  background-color: #333333;
  padding: 2.5rem;
  margin-bottom: 0;
}
.footer .container-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.no-webp .start {
  background-image: url(build/img/header.jpg);
}

.webp .start {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.start {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.start {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.start .container-header {
    min-height: 70rem;
  }
}

.container-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.container-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.right {
  display: flex;
  flex-direction: column;
}

.bar {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
  }
}

.nav {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  height: 0%;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.nav a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.nav a:hover {
  color: #E08709;
}
@media (min-width: 768px) {
  .nav a {
    margin-right: 2rem;
  }
  .nav a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .nav-footer {
    display: flex;
    align-items: center;
  }
}
.nav-footer a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.nav-footer a:hover {
  color: #E08709;
}
@media (min-width: 768px) {
  .nav-footer a {
    margin-right: 2rem;
  }
  .nav-footer a:last-of-type {
    margin-right: 0;
  }
}

.movile-menu {
  width: 5rem;
  display: flex;
  justify-content: space-around;
}
@media (min-width: 768px) {
  .movile-menu .bmenu {
    display: none;
  }
  .movile-menu .btnDarkmode {
    display: inline-block;
  }
}
.movile-menu .btnDarkmode {
  filter: invert(100%);
}
.movile-menu:hover {
  cursor: pointer;
}
.movile-menu img {
  margin: 0 1rem 0 1rem;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .icons-us {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .container-advertisements {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.advertisement {
  background-color: #eeeeee;
  border: 1px solid #fbfbfb;
}
.advertisement .container-advertisement {
  padding: 2rem;
}
.advertisement h3, .advertisement p {
  margin: 0;
}

.price {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.icons-caracteristics {
  display: flex;
  padding: 0;
  list-style: none;
  max-width: 40rem;
}
.icons-caracteristics li {
  flex: 1;
  display: flex;
}
.icons-caracteristics li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.icons-caracteristics li p {
  font-weight: 900;
}

.s1 {
  margin-bottom: 2rem;
}

.no-webp .img-contact {
  background-image: url(../img/encuentra.jpg);
}

.webp .img-contact {
  background-image: url(../img/encuentra.webp);
}

.img-contact {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.img-contact h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.img-contact p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .down-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.blog-entry {
  margin-bottom: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .blog-entry {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2.5rem;
  }
}
.blog-entry:last-of-type {
  margin-bottom: 0;
}
.blog-entry h4 {
  margin: 0;
  margin-top: 2rem;
  color: #000000;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .blog-entry h4 {
    margin-top: 0;
  }
}
.blog-entry h4::after {
  content: "";
  display: block;
  width: 25rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.blog-entry h4 .image picture {
  margin: auto;
}
.blog-entry h4 .image picture img {
  margin: auto;
}
.blog-entry p {
  margin-bottom: 0;
}

.meta-info span {
  color: #E08709;
  font-weight: 700;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: end;
  color: #FFFFFF;
  font-weight: 500;
}

.container-us {
  align-items: center;
}
@media (min-width: 768px) {
  .container-us {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    gap: 7rem;
  }
}

.us-text blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: 700;
  display: block;
  text-transform: uppercase;
}
.form input:not([type=submit], [type=radio]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

textarea {
  height: 20rem;
}

.contact-form {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.alert {
  padding: 0.5rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  margin: 0.4rem;
}
.alert.error {
  background-color: #fd8c8c;
}

.msj {
  color: #FFFFFF;
  text-align: center;
  font-weight: 700;
  padding: 0.5rem;
  font-size: medium;
  text-transform: uppercase;
}

.msj-container {
  background-color: #8b0000;
  width: 25%;
  margin: auto;
}